import React from 'react';
import { connect } from 'react-redux';
import { injectIntl } from 'react-intl';
import { Breadcrumb, Form, Input, Button, Row, Col, message, Tabs, Card, Select } from 'antd';
import {
  saveProfile,
  changePassword,
  getBillingInfo,
  addBillingInfo,
  editBillingInfo,
} from './../../services';
const countries = require('./../../utils/countries').default;

const { TabPane } = Tabs;
const Option = Select.Option;
const FormItem = Form.Item;

class ProfileCmp extends React.Component {
  _isMounted = false;

  state = {
    loadingv1: false,
    loadingv2: false,
    loadingv3: false,
    confirmDirty: false,
    formValues: {
      name: '',
      address: '',
      country: 'ES',
      vattax: '',
    },
  };

  componentDidMount = () => {
    const {
      authState: { user },
    } = this.props;
    if (user && user.rol && user.rol !== 'admin' && !user.UserId) {
      this.fetchBillingInfo();
    }
  };

  componentWillUnmount() {
    this._isMounted = false;
  }

  fetchBillingInfo = async () => {
    this._isMounted = true;
    try {
      this.setState({
        loadingv3: true,
      });
      const bi = await getBillingInfo();
      if (this._isMounted) {
        this.setState({
          formValues: bi.data.data,
          loadingv3: false,
        });
      }
    } catch (error) {
      message.error(error.message);
      this.setState({
        loadingv3: false,
      });
    }
  };

  handleSubmitFormBilling = e => {
    e.preventDefault();
    const {
      form,
      intl: { messages },
    } = this.props;
    const self = this;

    try {
      form.validateFields(['name', 'address', 'country', 'vattax'], async (err, values) => {
        if (!err) {
          self.setState({
            loadingv3: true,
          });

          values.continent = countries[values.country].continent;
          if (self.state.formValues.id) {
            values.id = self.state.formValues.id;
            await editBillingInfo(values);
          } else {
            await addBillingInfo(values);
          }

          self.setState({
            loadingv3: false,
          });
          message.success(messages.default.success);
        }
      });
    } catch (error) {
      self.setState({
        loadingv3: false,
      });
      message.error(error.message);
    }
  };

  handleSubmitForm = e => {
    e.preventDefault();
    const {
      form,
      intl: { messages },
    } = this.props;
    const self = this;
    form.validateFields(['fullName', 'email'], (err, values) => {
      if (!err) {
        self.setState({
          loadingv1: true,
        });
        saveProfile(values)
          .then(() => {
            self.setState({
              loadingv1: false,
            });
            message.success(messages.default.success);
          })
          .catch(err => {
            self.setState({
              loadingv1: false,
            });
            message.error(err.message);
          });
      }
    });
  };

  handleSubmitFormPass = e => {
    e.preventDefault();
    const {
      form,
      intl: { messages },
    } = this.props;
    const self = this;
    form.validateFields(['oldPassword', 'password', 'confirm'], (err, values) => {
      if (!err) {
        self.setState({
          loadingv2: true,
        });
        changePassword(values)
          .then(() => {
            self.setState({
              loadingv2: false,
            });
            message.success(messages.default.success);
          })
          .catch(error => {
            if (error.response.status === 400) {
              message.error(messages.signup.error.email);
            } else if (error.response.status === 401) {
              message.error(messages.profile.errorOldPassword);
            } else {
              message.error(messages.signup.error.all);
            }
            self.setState({
              loadingv2: false,
            });
          });
      }
    });
  };

  handleConfirmBlur = e => {
    const value = e.target.value;
    const aux = this.state.confirmDirty || !!value;
    this.setState({
      confirmDirty: aux,
    });
  };

  compareToFirstPassword = (rule, value, callback) => {
    const {
      form,
      intl: { messages },
    } = this.props;
    if (value && value !== form.getFieldValue('password')) {
      callback(messages.signup.rules.password.match);
    } else {
      callback();
    }
  };

  validateToNextPassword = (rule, value, callback) => {
    const { form } = this.props;
    if (value && this.state.confirmDirty) {
      form.validateFields(['confirm'], { force: true });
    }
    callback();
  };

  render() {
    const {
      intl: { messages },
      authState: { user },
      form: { getFieldDecorator },
    } = this.props;
    const { loadingv1, loadingv2, loadingv3, formValues } = this.state;

    const options = Object.keys(countries).map(key => {
      return (
        <Option key={key} value={key}>
          {countries[key].name}
        </Option>
      );
    });
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
        },
        sm: {
          span: 22,
        },
      },
    };

    return (
      <React.Fragment>
        <Card>
          <Tabs defaultActiveKey="profile">
            <TabPane tab={messages.profile.account} key="profile">
              <Row>
                <Col xs={24} md={12}>
                  <h3 style={{ textAlign: 'center' }}>{messages.profile.editProfile}</h3>
                  <Form
                    {...formItemLayout}
                    style={{ margin: '0 auto' }}
                    onSubmit={this.handleSubmitForm}
                    className="login-form"
                  >
                    <FormItem label="APIKey" hasFeedback>
                      {getFieldDecorator('apikey', {
                        initialValue: user.apikey,
                      })(<Input disabled type="text" />)}
                    </FormItem>
                    <FormItem label="Name" hasFeedback>
                      {getFieldDecorator('fullName', {
                        initialValue: user.fullName,
                        rules: [
                          {
                            required: true,
                            message: messages.signup.rules.fullName.required,
                          },
                        ],
                      })(<Input placeholder={messages.signup.fullName} type="text" />)}
                    </FormItem>
                    <FormItem label="Email" hasFeedback>
                      {getFieldDecorator('email', {
                        initialValue: user.email,
                        rules: [
                          {
                            type: 'email',
                            message: messages.signup.rules.email.type,
                          },
                          {
                            required: true,
                            message: messages.signup.rules.email.required,
                          },
                        ],
                      })(<Input placeholder="E-mail" />)}
                    </FormItem>

                    <FormItem {...tailFormItemLayout}>
                      <div>
                        <Button
                          style={{ float: 'right' }}
                          type="primary"
                          htmlType="submit"
                          loading={loadingv1}
                          className="login-form-button"
                        >
                          {!loadingv1 ? messages.default.save : messages.signup.btnSignUpLoad}
                        </Button>
                      </div>
                    </FormItem>
                  </Form>
                </Col>
                <Col xs={24} md={12}>
                  <h3 style={{ textAlign: 'center' }}>{messages.profile.changePassword}</h3>
                  <Form
                    style={{ margin: '0 auto' }}
                    onSubmit={this.handleSubmitFormPass}
                    className="login-form"
                  >
                    <FormItem hasFeedback>
                      {getFieldDecorator('oldPassword', {
                        rules: [
                          {
                            required: true,
                            message: messages.signup.rules.password.required,
                          },
                        ],
                      })(<Input placeholder={messages.profile.oldPassword} type="password" />)}
                    </FormItem>
                    <FormItem hasFeedback>
                      {getFieldDecorator('password', {
                        rules: [
                          {
                            required: true,
                            message: messages.signup.rules.password.required,
                          },
                          {
                            validator: this.validateToNextPassword,
                          },
                        ],
                      })(<Input placeholder={messages.signup.password} type="password" />)}
                    </FormItem>
                    <FormItem hasFeedback>
                      {getFieldDecorator('confirm', {
                        rules: [
                          {
                            required: true,
                            message: messages.signup.rules.password.confirm,
                          },
                          {
                            validator: this.compareToFirstPassword,
                          },
                        ],
                      })(
                        <Input
                          type="password"
                          placeholder={messages.signup.confirm}
                          onBlur={this.handleConfirmBlur}
                        />
                      )}
                    </FormItem>
                    <FormItem>
                      <div>
                        <Button
                          style={{ float: 'right' }}
                          type="primary"
                          htmlType="submit"
                          loading={loadingv2}
                          className="login-form-button"
                        >
                          {!loadingv2 ? messages.default.save : messages.signup.btnSignUpLoad}
                        </Button>
                      </div>
                    </FormItem>
                  </Form>
                </Col>
              </Row>
            </TabPane>
            {user && user.rol !== 'admin' && !user.UserId && (
              <TabPane tab={messages.profile.billinginformation} key="2">
                <Row>
                  <Col>
                    <h3 style={{ textAlign: 'center' }}>{messages.profile.billinginformation}</h3>
                    <Form
                      style={{ margin: '0 auto' }}
                      onSubmit={this.handleSubmitFormBilling}
                      className="login-form"
                    >
                      {/* nombre, dirección, pais, VAT/TAX number */}
                      <Form.Item>
                        {getFieldDecorator('name', {
                          initialValue: formValues.name,
                          rules: [
                            {
                              required: true,
                              message: 'Please input your Name!',
                            },
                          ],
                        })(<Input placeholder={messages.profile.name} />)}
                      </Form.Item>
                      <Form.Item>
                        {getFieldDecorator('address', {
                          initialValue: formValues.address,
                          rules: [
                            {
                              required: true,
                              message: 'Please input your Address!',
                            },
                          ],
                        })(<Input placeholder={messages.profile.address} />)}
                      </Form.Item>
                      <Form.Item hasFeedback>
                        {getFieldDecorator('country', {
                          initialValue: formValues.country,
                          rules: [
                            {
                              required: true,
                              message: 'Please select your country!',
                            },
                          ],
                        })(
                          <Select
                            showSearch
                            optionFilterProp="children"
                            filterOption={(input, option) =>
                              option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                            }
                            placeholder={messages.profile.country}
                          >
                            {options}
                          </Select>
                        )}
                      </Form.Item>
                      <Form.Item>
                        {getFieldDecorator('vattax', {
                          initialValue: formValues.vattax,
                          rules: [
                            {
                              required: true,
                              message: 'Please input your VAT/TAX number!',
                            },
                          ],
                        })(<Input placeholder={messages.profile.vattax} />)}
                      </Form.Item>
                      <FormItem>
                        <div>
                          <Button
                            style={{ float: 'right' }}
                            s
                            type="primary"
                            htmlType="submit"
                            loading={loadingv3}
                            className="login-form-button"
                          >
                            {!loadingv3 ? messages.default.save : messages.signup.btnSignUpLoad}
                          </Button>
                        </div>
                      </FormItem>
                    </Form>
                  </Col>
                </Row>
              </TabPane>
            )}
          </Tabs>
        </Card>
      </React.Fragment>
    );
  }
}

function mapStateToProps(state) {
  return {
    authState: state.rootReducer.auth,
  };
}

function mapDispatchToProps(dispatch) {
  return {};
}

const WrappedProfileCmp = Form.create()(injectIntl(ProfileCmp));

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(WrappedProfileCmp);
